.field-group--inline > .details-wrapper, .field-group--inline > .fieldset-wrapper {
  flex-flow: wrap;
  column-gap: 15px;
  display: flex;
}

.field-group--inline > .details-wrapper > *, .field-group--inline > .fieldset-wrapper > * {
  width: calc(25% - 11.25px);
  box-sizing: border-box;
}

@media (max-width: 999.98px) {
  .field-group--inline > .details-wrapper > *, .field-group--inline > .fieldset-wrapper > * {
    width: calc(50% - 7.5px);
  }
}

@media (max-width: 499.98px) {
  .field-group--inline > .details-wrapper > *, .field-group--inline > .fieldset-wrapper > * {
    width: 100%;
  }
}

.field-group--inline > .details-wrapper > .description, .field-group--inline > .fieldset-wrapper > .description {
  width: 100%;
}

.horizontal-tabs-pane > .details-wrapper {
  padding: 0;
}

